
#parent-swiper-container {
	margin-top: 20%;
}
.pagebox .swiper_pagination {
	position: absolute;
	top: 20%;
	bottom: inherit;
	left: 10%;
	width: 80%;
}
.swiper_pagination .swiper-pagination-custom {
	float: left;
	width: 33.3%;
}
.swiper-pagination-custom.active {
	color: #f05500;
}
.swiper-slide:before {
	content: "";
	line-height: 30vh;
}
.yj {
	width: 25%;
	position: absolute;
	top: 25%;
	left: 37.5%;
}
.swiper-container {
	height: 50vh;
}
.swiper-slide p {
	margin-top: 10px;
}
.swiper-slide-active>p {
	color: #f05500
}
.swiper-slide img {
	display: inline-block;
	vertical-align: middle;
	width: 50%;
	margin: 0 auto;
}
.pagebox {
	padding: 30px;
}
.question-subject {
	margin-bottom: 20px;
	text-align: left;
}
.next-subject {
	position: fixed;
	bottom: 30px;
	width: 33.3%;
	left: 33.3%;
	border: 1px solid #FFF;
	border-radius: 5px;
	padding: 10px 0;
	text-align: center;
}
.questionNum {
	color: #000;
	background: #FFF;
	width: 25px;
	height: 25px;
	display: inline-block;
	vertical-align: middle;
	border-radius: 50%;
	line-height: 25px;
	text-align: center;
	margin-right: 15px;
	font-size: 14px;
}



