
.selectTitle{
	    position: fixed;
    bottom: 10px;
    right: 30px;
    z-index: 1000;
}
.productContainer {
	 height: 100%;
    overflow: auto;
}
.back_box {
		width: 20%;
		height: 1rem;
		line-height: 1.25rem;
		font-size: 0.34rem;
		float: left;
		color: rgba(255, 255, 255, 0.8);
}
.back_box img {
		width: 0.35rem;
		opacity: 0.8;
		float: left;
		margin-top: 0.44rem;
		margin-left: 0.25rem;
		vertical-align: middle;
}
.back_box span {
		height: 1rem;
		float: left;
}
.get_back p {
		width: 60%;
		height: 1rem;
		line-height: 1.2rem;
		text-align: center;
		float: left;
		font-size: 0.4rem;
}
	/*商品区域*/
#main {
		overflow-y: scroll;
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		padding-bottom: 30px;
		padding-top: 1.75rem;
}
.container-fluid {
		padding: .5rem;
		padding-bottom: 2rem;
		position: relative
}
.container-fluid li.oneproduct {
		/*float: left;*/
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.container-fluid li.oneproduct .single img {
		max-width: 100%
}
.son {
		margin: 10px;
		background: red;
}
.noloaddate {
		position: absolute;
		bottom: 0;
		left: 0;
		text-align: center;
		width: 100%;
		color: #FFF;
		font-size: .75rem
}
.all_info {
		border: 3px solid rgba(255, 255, 255, .2);
}
.bg {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 9999;
		background: rgba(0, 0, 0, .3);
		display: none;
}
.productEditPop {
		z-index: 10000;
		display: none;
		font-size: .75rem;
		position: absolute;
		top: 30%;
		left: 50%;
		width: 5rem;
		margin-left: -2.5rem;
		background: rgba(255, 255, 255, .75);
		border-radius: 5px;
}
.productEditPop div {
		padding: .5rem 0;
		text-align: center;
		border-bottom: 1px solid #FFF;
		color: #0C6AFF;
}
.productEditPop div:last-child {
		border: 0px
}
.line p {
		font-size: 14px;
}
#footer {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 48px;
		background: #444;
		padding: 0;
		border-top: 1px solid #444;
}
	/*#scroller{height: 120%;}*/
.appendBox {
		-webkit-column-count: 2;
		column-count: 2;
		-webkit-column-gap: 0.5em;
		column-gap: 0.5em;
		padding: 10px;
}
.white_bgg:after {
		clear: both;
}
.white_bgg.selected {
		background-color: green;
}
.white_bgg {
		-webkit-column-break-inside: avoid;
		padding: 5px;
		margin-top: 10px;
		background-color: rgba(255, 255, 255, 0.26);
}
.commodity {
		width: 100%;
}
.picture {
		width: 100%;
		display: block;
}
	/*信息模块*/
.message_box {
		width: 100%;
		/*margin: 0 auto 3%;*/
		background-color: rgba(255, 255, 255, 0.26);
}
.message {
		width: 90%;
		margin: 0 auto;
}
.message>div {
		height: 0.6rem;
		line-height: 0.8rem;
		overflow: hidden;
}
.message p {
		padding-top: 5px;
		color: rgba(255, 255, 255, 0.6);
}
.mold {
		width: 100%;
		overflow: hidden;
		padding: 5px 0;
}
.mold li:first-child {
		width: 59%;
		border-right: 1px solid rgba(255, 255, 255, 0.3);
}
.mold li:last-child {
		width: 40%;
		text-indent: 0.1rem;
}
.mold li {
		float: left;
		font-size: 0.25rem;
		color: rgba(255, 255, 255, 0.5);
}
.collect {
		width: 50%;
		height: 0.5rem;
		line-height: 0.5rem;
		float: right;
}
.collect li {
		width: 50%;
		font-size: 0.3rem;
		float: left;
		color: rgba(255, 255, 255, 0.5);
}
.collect li img {
		width: 0.33rem;
		margin-right: 0.05rem;
		vertical-align: middle;
}
#footer {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 0.8rem;
		background-color: #222;
		background-image: -o-linear-gradient(top, #999, #666 2%, #222);
		padding: 0;
		border-top: 1px solid #444;
}
.Shopping-Mall {
		width: 100%;
		height: 100%;
		display: none;
		position: fixed;
		bottom: 0;
		left: 0;
		background-size: cover;
}
.shopping-mall-container {
		width: 80%;
		height: 70%;
		position: absolute;
		top: 12%;
		left: 10%;
		background: rgba(0, 0, 0, .5);
}
.Shopping-Mall-title {
		height: 10%;
		width: 100%;
		display: table;
		border-bottom: 1px solid rgba(255, 255, 255, .5);
}
.Shopping-Mall-title span {
		display: table-cell;
		vertical-align: middle;
}
.mallclass {
		float: left;
		width: 50%;
		height: 90%;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.mall-left-class {
		border-right: 1px solid rgba(255, 255, 255, .5);
}
.classification {
		padding: 10%;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.classification img {
		width: 25%;
		margin: 0 auto;
		display: block;
}
.classification p {
		margin-top: 10%;
}
.mall-left-class .classification {
		height: 33%;
}
.mall-left-class .classification img {
}
.mall-left-class .classification:nth-child(1),
	.mall-left-class .classification:nth-child(2) {
		border-bottom: 1px solid rgba(255, 255, 255, .5);
}
.mall-right-class .classification {
		height: 25%;
}
.mall-right-class .classification:nth-child(1),
	.mall-right-class .classification:nth-child(2),
	.mall-right-class .classification:nth-child(3) {
		border-bottom: 1px solid rgba(255, 255, 255, .5);
}
	/*底部导航*/
li {
		list-style: none;
}
.footer_navigation {
		width: 100%;
		height: 10%;
		position: fixed;
		bottom: 0;
		z-index: 10;
		background: url(../../static/image/xl@2x.png) no-repeat;
		background-size: 100% 100%;
}
.footer_navigation ul {
		height: 98%;
		line-height: 1;
}
.footer_navigation ul li:first-child {
		float: left;
}
.footer_navigation ul li:last-child {
		float: right;
}
.footer_navigation ul li img {
		vertical-align: middle;
}
.footer_navigation ul li.bottom_nav {
		width: 50%;
		margin: 0 auto;
		text-align: center;
		margin-left: -25%;
		left: 50%;
		position: absolute;
		height: 100%;
}
.bottom_nav img {
		width: 10%;
		margin-top: 10px;
		margin-bottom: 10px;
}
.bottom_nav span {
		display: block;
		font-size: 0.26rem;
		height: 0.25rem;
		line-height: 0.25rem;
		margin-top: 0.26rem;
		color: #fff;
}
.footer_navigation ul li.back_more {
		position: absolute;
		top: 50%;
		left: 10%;
}
.footer_navigation ul li.back_more img {
		width: 1.5em;
}
.bgBody.filter {
		-webkit-filter: blur(3px);
		        filter: blur(3px);
}



